/*=====================
  Базовая типография
=====================*/
@font-face {
  font-family: "Panton";
  src: local("Panton"), url("../fonts/panton.ttf") format("truetype"), url("../fonts/panton.woff") format("woff"), url("../fonts/panton.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Panton Semibold";
  src: local("Panton Semibold"), url("../fonts/panton-semibold.ttf") format("truetype"), url("../fonts/panton-semibold.woff") format("woff"), url("../fonts/panton-semibold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Helveticaneuecyr Roman";
  src: local("Helveticaneuecyr Roman"), url("../fonts/helveticaneuecyr-roman.ttf") format("truetype"), url("../fonts/helveticaneuecyr-roman.woff") format("woff"), url("../fonts/helveticaneuecyr-roman.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #646464;
  font-weight: normal;
}

.btn {
  display: inline-block;
  cursor: pointer;
  text-align: center;
  vertical-align: top;
  text-decoration: none;
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: white;
  font-weight: normal;
  background: linear-gradient(#feab16, #ff7500) #ff9000;
  padding: 10px 30px;
  border-radius: 4px;
  border: 2px solid #ff9000;
  transition: all 0.3s ease;
  box-shadow: 0px 0px 3px 1px #D2D2D2;
}
.btn:hover {
  background: #ff9451;
}
.btn:active {
  background: #f5843e;
}

.btn--call {
  float: right;
  max-width: 210px;
}

.btn--features {
  min-width: 155px;
}

.btn--houses {
  min-width: 155px;
  margin-left: 60px;
}

.btn--form-submit {
  width: 390px;
  box-sizing: border-box;
  height: 55px;
  font-size: 20px;
  text-shadow: -1px 1px 1px #444;
}

input[type="text"],
textarea,
select,
input[type="password"] {
  border: none;
  outline: none;
  background: white;
  box-shadow: 1px -1px 1px 0px #eceaea;
  border: 1px solid #f1f2f6;
  box-sizing: border-box;
  width: 100%;
  height: 55px;
  color: #646464;
  border-radius: 4px;
}

label {
  cursor: pointer;
}

button {
  outline: none;
  cursor: pointer;
}

input::-webkit-input-placeholder {
  opacity: 0.8;
}

input::-moz-placeholde {
  opacity: 0.8;
}

select {
  appearance: none;
  background: white url("../img/arrow-select.png") no-repeat right;
  cursor: pointer;
  padding-right: 15px;
  padding-left: 15px;
}

/*=== компоненты ===*/
.header {
  padding: 30px 0;
}

.logo {
  float: left;
  width: 113px;
}
.logo img {
  display: block;
}

.menu {
  float: left;
  width: 710px;
  margin-left: 60px;
}
.menu__list {
  padding: 0;
  list-style: none;
  font-size: 0;
  margin-top: 10px;
}
.menu__item {
  display: inline-block;
  vertical-align: top;
  margin-right: 54px;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__link {
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #192d44;
  font-weight: normal;
  text-decoration: none;
  padding-bottom: 5px;
}
.menu__link:hover {
  border-bottom: 1px solid #192d44;
}

.promo {
  width: 100%;
  height: 954px;
  background-image: url("../img/promo-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
  padding-top: 45px;
  position: relative;
}
.promo__link {
  font-size: 0;
  background: url("../img/scroll.png") no-repeat;
  width: 33px;
  height: 48px;
  display: block;
  margin: 0 auto;
}

.promo-content {
  position: relative;
  margin-bottom: 300px;
}
.promo-content:after {
  content: "";
  display: table;
  clear: both;
}
.promo-content__image {
  width: 702px;
  height: 245px;
  float: right;
}
.promo-content__image img {
  display: block;
}
.promo-content__title {
  font-size: 24px;
  line-height: 27px;
  font-weight: normal;
  color: white;
  position: absolute;
  bottom: 5px;
  right: 25px;
  text-shadow: -1px 1px 2px #232323;
}

.features-block {
  padding-bottom: 115px;
}

.features__title {
  font-family: "Panton", Arial, sans-serif;
  font-size: 50px;
  line-height: 50px;
  color: #ff9000;
  margin: 0;
  transform: translateY(-20px);
  margin-bottom: 55px;
}

.features {
  font-size: 0;
}

.feature {
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 17px;
  line-height: 21px;
  display: inline-block;
  vertical-align: top;
  width: 260px;
  margin-right: 40px;
}
.feature:last-child {
  margin-right: 0;
}
.feature__title {
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: #ff9000;
  margin: 0;
  margin: 50px 0 40px;
  font-weight: normal;
}
.feature:before {
  content: "";
  display: block;
}
.feature--1:before {
  background: url("../img/feature1.png") no-repeat;
  width: 108px;
  height: 87px;
  margin-left: 56px;
}
.feature--2:before {
  background: url("../img/feature2.png") no-repeat;
  width: 76px;
  height: 85px;
  margin-left: 56px;
}
.feature--3:before {
  background: url("../img/feature3.png") no-repeat;
  width: 85px;
  height: 85px;
  margin-left: 50px;
}
.feature--4:before {
  background: url("../img/feature4.png") no-repeat;
  width: 85px;
  height: 85px;
  margin-left: 76px;
}
.feature__tip {
  color: #818181;
}
.feature__description {
  margin: 0;
  margin: 15px 0 27px;
}

.slider {
  background-image: url("../img/slider-bg.jpg");
  background-color: #1d2734;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
}
.slider__controls {
  position: absolute;
  bottom: 10px;
  transform: translateX(-50%);
  left: 50%;
  font-size: 0;
}
.slider__control {
  width: 13px;
  height: 7px;
  background: #e8e8e8;
  margin-right: 10px;
  display: inline-block;
  vertical-align: top;
  border-radius: 1px;
}
.slider__control:last-child {
  margin-right: 0;
}
.slider__control--active {
  background: linear-gradient(#feab16, #ff7500) #ff9000;
}
.slider__title {
  font-family: "Panton", Arial, sans-serif;
  font-size: 30px;
  line-height: 15px;
  color: #e7e7e7;
  font-weight: normal;
  width: 732px;
  margin: 0;
  margin: 35px 0 25px;
}
.slider__description {
  color: #b0b0b0;
  font-size: 20px;
  line-height: 25px;
  width: 732px;
  margin: 0;
}

.slide {
  padding: 70px 0 85px;
  position: relative;
}
.slide:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}
.slide--1:after {
  background: url("../img/slider-img-1.png") no-repeat;
  width: 310px;
  height: 399px;
}
.slide--2 {
  display: none;
}
.slide--2:after {
  background: url("../img/slider-img-2.png") no-repeat;
  width: 279px;
  height: 409px;
}
.slide--3 {
  display: none;
}
.slide--3:after {
  background: url("../img/slider-img-3.png") no-repeat;
  width: 332px;
  height: 418px;
}

.our-houses {
  padding-top: 85px;
  padding-bottom: 70px;
}
.our-houses__title {
  font-family: "Panton", Arial, sans-serif;
  font-size: 50px;
  line-height: 50px;
  color: #ff9000;
  margin: 0;
  margin: 0;
  padding-left: 60px;
}
.our-houses__description {
  color: #646464;
  font-size: 20px;
  line-height: 30px;
  margin: 0;
  width: 920px;
  margin-top: 25px;
  padding-left: 60px;
}

.houses {
  font-size: 0;
  margin-top: 85px;
}

.house {
  display: inline-block;
  vertical-align: top;
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #646464;
  font-weight: normal;
  width: 350px;
  margin-right: 55px;
}
.house:last-child {
  margin-right: 0;
}
.house__image {
  width: 350px;
  height: 226px;
}
.house__image img {
  display: block;
}
.house__title {
  padding-left: 60px;
  font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: #ff9000;
  margin: 0;
  margin: 15px 0 20px;
}
.house__description {
  padding-left: 60px;
  padding-right: 35px;
  margin-bottom: 30px;
}

.advantages-block {
  background-image: url("../img/advantages.jpg");
  background-color: #1d2734;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 90px;
  padding-bottom: 50px;
}
.advantages-block__title {
  font-family: "Panton", Arial, sans-serif;
  font-size: 50px;
  line-height: 50px;
  color: #ff9000;
  margin: 0;
  margin-bottom: 40px;
}
.advantages-block__description {
  color: #646464;
  font-size: 20px;
  line-height: 30px;
  margin: 0;
  color: #d0d0d0;
  margin-bottom: 75px;
}

.advantages {
  font-size: 0;
}

.advantage {
  display: inline-block;
