}
.advantage--2:before {
  background: url("../img/why-icon-2.png") no-repeat;
  width: 42px;
  height: 38px;
  left: 100px;
}
.advantage--3:before {
  background: url("../img/why-icon-3.png") no-repeat;
  width: 40px;
  height: 38px;
  left: 110px;
}
.advantage--4:before {
  background: url("../img/why-icon-4.png") no-repeat;
  width: 54px;
  height: 54px;
  left: 100px;
}
.advantage--5:before {
  background: url("../img/why-icon-5.png") no-repeat;
  width: 40px;
