}
.advantage__btn-link {
  font-size: 15px;
  color: #029dff;
  display: block;
  margin-top: 3px;
}

.calculation {
  background: #f9faff;
  padding: 85px 0 0;
}
.calculation__title {
  font-family: "Panton", Arial, sans-serif;
  font-size: 50px;
  line-height: 50px;
  color: #ff9000;
  margin: 0;
  position: relative;
  padding-bottom: 50px;
  margin-bottom: 23px;
}
.calculation__title:after {
  content: "";
  display: block;
  width: 368px;
  height: 226px;
  background: url("../img/calc-house.png") no-repeat;
  position: absolute;
