.management-slider {
  background-color: #ffffff;
  position: relative;
}
.management-slider__controls {
  position: absolute;
  bottom: 35px;
  transform: translateX(-50%);
  left: 50%;
  font-size: 0;
}
.management-slider__control {
  width: 13px;
  height: 7px;
  background: #e8e8e8;
  margin-right: 10px;
  display: inline-block;
  vertical-align: top;
  border-radius: 1px;
}
.management-slider__control:last-child {
  margin-right: 0;
}
.management-slider__control--active {
  background: linear-gradient(#feab16, #ff7500) #ff9000;
}

.management-slide {
  padding: 90px 0 95px;
  position: relative;
}
.management-slide:after {
  content: "";
  display: table;
  clear: both;
}
.management-slide__image {
  width: 214px;
  height: 214px;
  border-radius: 50%;
  float: left;
  margin-right: 75px;
}
.management-slide__image img {
  display: block;
}
.management-slide__content {
  float: left;
  width: 680px;
}
.management-slide__name {
  font-size: 27px;
  line-height: 29px;
  color: #ff9000;
  padding-top: 5px;
}
.management-slide__post {
  font-size: 15px;
  margin-top: 15px;
}
.management-slide__description {
  font-size: 18px;
  line-height: 24px;
  margin: 20px 0;
}
.management-slide__footer:after {
  content: "";
  display: table;
  clear: both;
}
.management-slide__footer-content {
  font-size: 15px;
  width: 330px;
  float: left;
}
.management-slide__footer-link {
  float: left;
  font-size: 15px;
  display: block;
  width: 260px;
  padding-left: 25px;
  color: #3d53c3;
  position: relative;
}
.management-slide__footer-link:before {
  position: absolute;
  left: 0;
  top: 5px;
  content: "";
  display: block;
  background: url("../img/mail-icon.png") no-repeat;
  width: 15px;
  height: 12px;
}
.management-slide--2 {
  display: none;
}

